<template>
    <div id="histories">
        <keep-alive>
            <div class="table-responsive">
                <caption class="history_headers">
                    <img v-if="publicDisplay" class="user-public-avatar" :src="userProfileAvatar" @error="avatarError"/>
                    <h3 v-html="getTrans('history_caption' + (publicDisplay ? '_public' : ''), {'%game%': gameName, '%login%': userProfileName})"></h3><small>{{ getTrans('history_sub_caption') }}&nbsp;<input type="checkbox" @click="filterAborted" :checked="currentFilter"/></small><br/>
                    <ul class="periods pagination">
                        <li :class="{active: currentPeriod === 'lastgames'}" class="first"><a href="#" @click.prevent="switchPeriod('lastgames')" :disabled="currentPeriod === 'lastgames'">{{ getTrans('last_15_games') }}</a></li>
                        <template v-if="summaries && aggregateSummaries"><li v-for="data in aggregateSummaries" :key="data.year" :class="{active: currentPeriod === parseInt(data.year)}"><a href="#" @click.prevent="switchPeriod(data.year)" :disabled="currentPeriod === parseInt(data.year)">{{ data.year }} ({{ currentFilter ? data.count_finished : data.count }})</a></li></template>
                    </ul>
                </caption>
                <template v-if="histories !== null && histories.length > 0"><pagination :lang="lang" :current-page="currentPage" :page-count="pageCount" :size="$mq | mq({ xxs: 1, md: 9, xl: 15 })" @navigate="navigate"></pagination></template>
                <table class="table table-striped">
                    <template v-if="histories === null">
                        <tbody>
                            <tr>
                                <td colspan="5" class="centercol"><LoadingComp></LoadingComp></td>
                            </tr>
                        </tbody>
                    </template>
                    <template v-else>
                        <template v-if="histories.length === 0">
                            <tbody>
                                <tr>
                                    <td v-if="!summaries" colspan="5" class="centercol" v-html="getTrans('history_no_games' + (publicDisplay ? '_public' : ''), {'%login%': userProfileName})"></td>
                                    <td v-else colspan="5" class="centercol" v-html="getTrans('history_no_games_on_period' + (publicDisplay ? '_public' : ''), {'%login%': userProfileName})"></td>
                                </tr>
                            </tbody>
                        </template>
                        <template v-else>
                            <thead>
                                <tr>
                                    <td>{{ getTrans('date') }}</td>
                                    <td>{{ getTrans('score') }}</td>
                                    <td>{{ getTrans('other_players') }}</td>
                                    <td>{{ getTrans('variant') }}</td>
                                    <td>{{ getTrans('ranked_game') }}</td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="history in histories" :key="history.id">
                                    <td>
                                        <time :data-timestamp="history.date"><template v-if="['xs', 'xxs'].includes($mq)">{{ $moment(history.date).format('L') }}</template><template v-else>{{ $moment(history.date).format('LL') }}</template></time>
                                    </td>
                                    <td>
                                        <span>{{ history.score }}</span>
                                    </td>
                                    <td class="other_players">
                                        <span v-for="player in history.other_players" :key="player.id"><a :href="urlFor('profile-user', {id: player.id})">{{ player.login_name }}</a> ({{ player.score }})</span>
                                    </td>
                                    <td><span v-html="getVariant(history.variant)"></span></td>
                                    <td><span class="ranked-checked"><font-awesome-icon icon="check" v-if="history.options.split(',').includes('rated')" /></span></td>
                                </tr>
                            </tbody>
                        </template>
                    </template>
                </table>
                <template v-if="histories !== null && histories.length > 0"><pagination :lang="lang" :current-page="currentPage" :page-count="pageCount" :size="$mq | mq({ xxs: 1, md: 9, xl: 15 })" @navigate="navigate"></pagination></template>
            </div>
        </keep-alive>
    </div>
</template>

<script>
import HTTP from '@/httpapi';
import axios from 'axios';
import Pagination from '@/components/Pagination';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

export default {
    name: 'game-histories',

    props: ['config'],

    mixins: [GenericMixin],

    components: {
        pagination: Pagination,
        'font-awesome-icon': FontAwesomeIcon
    },

    computed: {
        gameId () {
            return this.config.game.id;
        },

        gameName () {
            return this.config.game.name;
        },

        pageCount () {
            return Math.ceil(this.itemCount / this.itemsPerPage);
        },

        userProfileName () {
            return this.publicDisplay ? this.config.userProfile.login : null;
        },

        userProfileId () {
            return this.config.userProfile ? this.config.userProfile.id : 'me';
        },

        userProfileAvatar () {
            return this.publicDisplay ? this.config.userProfile.avatar : null;
        },

        publicDisplay () {
            return (this.userProfileId !== 'me');
        },

        search () {
            let s = '';
            if (this.publicDisplay) {
                s = '?u=' + this.userProfileId;
            }
            if (this.currentFilter) {
                s = s + (s === '' ? '?' : '&');
            }

            return s;
        },

        aggregateSummaries () {
            if (this.summaries === null) {
                return [];
            }

            let aggrega = {};
            let that = this;
            Object.keys(this.summaries).forEach((variant) => {
                if (that.gameId === 'TT' && ['TT_2p', 'TT_mp'].includes(variant)) {
                    return;
                }

                Object.keys(that.summaries[variant]).forEach((year) => {
                    if (aggrega[year] === undefined) {
                        aggrega[year] = {year: year, count: 0, count_finished: 0};
                    }
                    aggrega[year].count += that.summaries[variant][year].count;
                    aggrega[year].count_finished += that.summaries[variant][year].count_finished;
                });
            });
            let aggregafinal = [];
            Object.keys(aggrega).sort().reverse().map((y) => {
                aggregafinal.push(aggrega[y]);
            });

            return aggregafinal;
        }
    },

    methods: {
        initPage () {
            let that = this;
            axios.all([
                User.onlineGames(this.userProfileId, this.gameId),
                this.prepareNavigate(this.currentPage)
            ]).then(axios.spread((onlineGamesResults, navigateResult) => {
                that.summaries = onlineGamesResults.data.summaries;
                that.treatNavigate(navigateResult, this.currentPage);
            }));
        },

        prepareNavigate (requestedPage) {
            let args = [this.userProfileId, this.gameId, (this.currentFilter ? 'ended' : 'ended,disconnected')];

            let func = null;
            if (this.currentPeriod === 'lastgames') {
                func = User.lastgames;
            } else {
                func = User.history;
                args.push(this.currentPeriod);
            }
            args.push(requestedPage);
            args.push(this.itemsPerPage);

            this.histories = null;
            this.itemCount = null;

            return func(...args);
        },

        treatNavigate (result, requestedPage) {
            this.histories = result.data.games;
            this.itemCount = result.data.total;

            // Now we check if the requested page is reachable. If it is,
            // then fine, otherwise we go back to the closest possible page
            // (usually the last one).
            const currentPage = Math.max(Math.min(requestedPage, this.pageCount), 1);
            if (currentPage === requestedPage) {
                this.currentPage = requestedPage;
            } else {
                this.navigate(currentPage);
            }
        },

        navigate (requestedPage) {
            this.prepareNavigate(requestedPage).then((result) => this.treatNavigate(result, requestedPage));
        },

        switchPeriod (period) {
            this.currentPeriod = period !== 'lastgames' ? parseInt(period) : period;
            this.navigate(1);
        },

        filterAborted () {
            this.currentFilter = !this.currentFilter;
            this.navigate(1);
        },

        getVariant (variant) {
            switch (this.gameId) {
            case 'TT':
                // return img flag
                if (variant) {
                    return '<img src="https://ncdn1.daysofwonder.com/fr/img/variant_' + variant + '.gif" title="' + variant + '" alt="' + variant + '"/>';
                }
                break;

            case 'MM':
                if (!variant) {
                    return 'online';
                }
                break;
            }
            if (variant) {
                return variant;
            }

            return '&nbsp;';
        }
    },

    watch: {
        // Page URL structure: /games/history(/period)(/:page)(?filter)
        currentPage (page, oldPage) {
            // Sync the last part of the URL with the currently displayed page
            const elements = window.location.pathname.split('/');

            if (elements[elements.length - 1] === '' + oldPage && oldPage !== this.currentPeriode) {
                elements.pop();
            }
            elements.push(page);

            window.history.replaceState({}, '', elements.join('/') + this.search + (this.currentFilter ? 'noaborted=1' : ''));
        },

        currentPeriod (period, oldPeriod) {
            const elements = window.location.pathname.split('/');

            if (oldPeriod === 'lastgames') {
                if (elements[elements.length - 1] === ('' + this.currentPage)) {
                    elements[elements.length] = '' + this.currentPage;
                    elements[elements.length - 2] = 'lastgames';
                } else {
                    elements[elements.length] = 'lastgames';
                    elements[elements.length] = '' + this.currentPage;
                }
            }

            let posPeriod = 2;
            if (!elements[elements.length - posPeriod].match(/^20\d\d$/)) {
                if (elements[elements.length - 1].match(/^20\d\d$/)) {
                    posPeriod = 1;
                }
            }

            if (period !== 'lastgames') {
                elements[elements.length - posPeriod] = period;
            } else {
                elements[elements.length - posPeriod] = this.currentPage;
                elements.pop();
            }

            window.history.replaceState({}, '', elements.join('/') + this.search + (this.currentFilter ? '?noaborted=1' : ''));
        },

        currentFilter (filter, oldFilter) {
            if (filter) {
                window.history.replaceState({}, '', window.location.pathname + this.search + 'noaborted=1');
            } else {
                window.history.replaceState({}, '', window.location.pathname + this.search);
            }
        }
    },

    mounted () {
        HTTP.ifInitialized(this.initPage);
    },

    data () {
        return {
            summaries: null,
            histories: null,
            itemsPerPage: 20,
            currentPage: this.config.page,
            itemCount: 0,
            currentPeriod: this.config.period || 'lastgames',
            currentFilter: this.config.filter || null
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/table.scss';
@import '~sass/text.scss';

#histories {
    .user-public-avatar {
        float: left;
        display: block;
        width: 100px;
        height: 100px;
        margin: 0 auto 10px;
        border-radius: 50%;
        background-color: #ffffff;
        z-index: 1001;
    }

    @include media-breakpoint-down(sm) {
        .user-public-avatar {
        width: 40px;
        height: 40px;
        margin: 0 auto 5px;
        }
    }

    .history_headers {
        display: block;
        text-align: center;
        width: 100%;

        .periods {
            display:inline-flex;
            flex-wrap: wrap;
            justify-content: center;

            a {
                margin-left: 10px;
                margin-right: 10px;
                white-space: nowrap;
            }
        }

        .active {
            a {
                color: white;
            }
        }
    }

    table {
        text-align: center;

        .other_players {
            display: flex;
            flex-wrap: nowrap;
            justify-content: space-around;
        }

        .ranked-checked {
            color: green;
        }
    }
}
</style>



